<template>
  <div class="fullLoader h-screen w-full fixed flex justify-center items-center bg-black text-white">
    <img src="../assets/img/Too small clock.png" v-animate-css="animations[1]" alt />
    <img src="../assets/img/Small clock.png" v-animate-css="animations[2]" alt />
    <img src="../assets/img/Large clock.png" v-animate-css="animations[3]" alt />
  </div>
</template>

<script>
const animations = {
  1: {
    classes: "zoomIn",
    duration: 300
  },
  2: {
    classes: "zoomIn",
    delay: 1200,
    duration: 300
  },
  3: {
    classes: "zoomIn",
    delay: 2400,
    duration: 300
  }
};

export default {
  name: "FullLoader",
  data() {
    return {
      animations
    };
  },
  mounted() {
    this.setClock();
  },
  methods: {
    setClock() {
      setTimeout(() => {
        this.$emit("loaderStatus", false);
      }, 3600);
    }
  }
};
</script>


<style scoped>
img {
  position: absolute;
}
</style>