      <div class="w-full hidden lg:block lg:w-3/6 px-5">
        <div class="min-h-screen flex flex-col justify-center self-start items-center">
          <div class="font-mons font-semibold text-3xl mt-auto self-start leading-10 pt-3 ml-10">
            <span class="text-blue">S</span>uresh
            <br />
            <span class="text-blue">B</span>abu
          </div>
          <div class="flex items-end mt-auto">
            <img
              src="../assets/img/Pic in landing.png"
              @mouseup="mainMouseUp"
              @mousedown="mainMouseDown"
              @mousemove="mainHover"
              @mouseout="mainHoverOut"
              class="flex-shrink-0 lg:w-3/4 xl:w-3/4 cursor-pointer"
            />
            <img
              src="../assets/img/UI-UX Designer.png"
              class="flex-shrink-0 self-center lg:w-1/4 xl:w-1/4"
              alt
            />
          </div>
        </div>
      </div>
      <div class="w-full lg:w-3/6 px-5">
        <div class="min-h-screen flex flex-col items-center justify-center">
          <div class="ml-auto mt-3 mb-5">
            <a
              href="../assets/img/MyResume.svg"
              class="cursor-pointer"
              target="_blank"
              download="SureshResume"
            >
              <img
                class="h-24 vImg"
                :src="resumeImg"
                @mouseover="hoverOver"
                @mouseout="hoverOut"
                alt
              />
            </a>
          </div>
          <section class="py-3 my-auto">
            <div class="py-3 sm:py-5">
              <div class="flex justify-center lg:hidden">
                <img src="../assets/img/Pic in landing.png" class="flex-shrink-0 w-2/3" />
              </div>
              <div class="text-blue text-2xl sm:text-3xl font-semibold">Hello!!</div>
              <div class="font-medium text-base sm:text-xl leading-7 sm:leading-9">
                I'm
                <span class="text-blue-100">SureshBabu.</span> A designer focused
                <br />on user needs & experience.
              </div>
            </div>
            <div class="flex flex-wrap py-3 sm:py-5">
              <router-link
                to="/SB/Portfolio"
                class="bg-black p-2 border-2 border-blue text-sm sm:text-base rounded-md font-semibold mb-3 sm:mb-0 w-full sm:w-48 text-center block sm:mr-3"
              >PORTFOLIO</router-link>
              <a
                href="mailto:babusuresh250@gmail.com"
                class="bg-blue p-2 border-2 border-blue text-sm sm:text-base rounded-md w-full font-medium sm:w-48 text-center block"
              >HIRE ME</a>
            </div>
            <div class="flex justify-center lg:justify-start py-3 sm:py-6">
              <a
                :href="icon.link"
                target="_blank"
                class="border-2 flex justify-center items-center rounded-full w-10 text-xl h-10 hover:border-blue focus:outline-none hover:bg-blue cursor-pointer mr-3"
                v-for="(icon,icI) in Icons"
                :key="icI"
              >
                <i :class="icon.icons" />
              </a>
            </div>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Landing",
  data() {
    return {
      Icons: [
        {
          icons: "fab fa-behance",
          link: "https://www.behance.net/sureshbabu27"
        },
        {
          icons: "fab fa-instagram",
          link: "https://www.instagram.com/sureshbabu_ssb"
        },
        {
          icons: "fab fa-dribbble",
          link: "https://dribbble.com/sureshbabussb"
        },
        {
          icons: "fab fa-linkedin-in",
          link: "https://www.linkedin.com/in/sureshbabu-s-78ba81113"
        }
      ],
      resumeImg: require("../assets/img/Component9-1.png"),
      timeOuts: undefined
    };
  },
  mounted() {},
  methods: {
    hoverOver(e) {
      e.target.style.transform = "rotate(-54.3deg)";
      this.timeOuts = setTimeout(() => {
        e.target.style.transition = "0s ease";
        e.target.style.transform = "none";
        this.resumeImg = require("../assets/img/Component9.png");
      }, 600);
    },
    hoverOut(e) {
      clearTimeout(this.timeOuts);
      e.target.style.transition = "0.6s ease";
      e.target.style.transform = "none";
      this.resumeImg = require("../assets/img/Component9-1.png");
    },
    mainHover(e) {
      const height = e.target.clientHeight;
      const width = e.target.clientWidth;
      const xVal = e.layerX;
      const yVal = e.layerY;
      const yRotation = 20 * ((xVal - width / 2) / width);
      const xRotation = -20 * ((yVal - height / 2) / height);
      const string =
        "perspective(500px) scale(1.1) rotateX(" +
        xRotation +
        "deg) rotateY(" +
        yRotation +
        "deg)";
      e.target.style.transform = string;
    },
    mainHoverOut(e) {
      e.target.style.transform =
        "perspective(500px) scale(1) rotateX(0) rotateY(0)";
    },
    mainMouseDown(e) {
      e.target.style.transform =
        "perspective(500px) scale(0.9) rotateX(0) rotateY(0)";
    },
    mainMouseUp(e) {
      e.target.style.transform =
        "perspective(500px) scale(1.1) rotateX(0) rotateY(0)";
    }
  }
};
</script>
